import { Link } from 'react-scroll'

export default function Menu({ link, text }) {
  return (
    <li style={{ cursor: 'pointer', listStyleType: 'none' }}>
      <Link
        activeClass='active'
        to={link}
        // spy={true} // Link     scroll
        smooth={true} // 
        duration={500} //  
      >
        {text}
      </Link>
    </li>
  )
}
