import {
  Drawer,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  DrawerHeader,
  DrawerBody,
  DrawerFooter,
  Button,
  Stack,
} from '@chakra-ui/react'

import Menu from './/Menu2'

export default function MenuDrawer({ btnRef, isDrawerOpen, onDrawerClose }) {
  const textMenu = [
    { text: ' ', link: 'start' },
    { text: ' ', link: 'aboutMe' },
    { text: '', link: 'portfolio' },
    { text: '', link: 'reviews' },
    { text: '', link: 'aboutMe' },
    { text: '', link: 'aboutMe' },
  ]
  return (
    //  
    <Drawer
      isOpen={isDrawerOpen}
      placement='right'
      onClose={onDrawerClose}
      finalFocusRef={btnRef} // 
    >
      <DrawerOverlay />
      <DrawerContent bg='rgba(202, 200, 185, 0.9)'>
        <DrawerCloseButton />
        <DrawerHeader></DrawerHeader>

        <DrawerBody>
          <Stack
            direction={{ base: 'column', lg: 'row' }}
            fontWeight='300'
            spacing={{ base: '0.1em', lg: '0em' }}
            fontSize={{ base: 'xl', lg: '2xl' }}
          >
            {textMenu.map((e) => (
              <Menu link={e.link} key={e} text={e.text} />
            ))}
          </Stack>
        </DrawerBody>

        {/*   */}
        <DrawerFooter>
          <Button
            variant='outline'
            colorScheme='rgb(48 42 38)'
            mr={3}
            color='rgb(48 42 38)'
            onClick={onDrawerClose}
          >
             
          </Button>
        </DrawerFooter>
      </DrawerContent>
    </Drawer>
  )
}
